.header-brand {
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}

.hamburger-btn {
    display: none;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: space-evenly;
        -ms-flex-pack: space-evenly;
            justify-content: space-evenly;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    height: 80%;
    width: 4rem;
}

.hamburger-btn div{
    width: 2.5rem;
    background-color: #ce2a56;
    height: 5px;
    border-radius: 5px;
}

.hamburger-btn:hover{
    cursor: pointer;
}

.header-brand__link {
    display: inline-block;
    height: 100%;
}

.header-brand__link:hover {
    cursor: pointer;
}

.header-brand__link img {
    height: 100%;
}

@media screen and (max-width: 700px) {
    .main-nav{
        display: none;
    }

    .hamburger-btn {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
    }
}
